import ReactDOM from 'react-dom'
import React, { useState, useRef, useEffect } from 'react'
import { useSpring, animated } from 'react-spring'

const AnimFeTurbulence = animated('feTurbulence')
const AnimFeDisplacementMap = animated('feDisplacementMap')

export default function BodyBack(props, {forwardedRef}) {
     let svgRef =  useRef(null)
     // 0.0175, 0
     const [open, toggle] = useState(false)
     const { freq, scale, transform, opacity } = useSpring({
          reverse: false,
          reset:true,
          from: { scale: 10, opacity: 1, transform: 'scale(1) ', freq: '0.001, 0.0' },
          to: { scale: 150, opacity: 1, transform: 'scale(1)', freq: '0.0, 0.0'},
          config: { duration: 1500 }
     })

  
     
     return (
          <div className="body--back ">
               <div className="svg--circular svg--color--change" onClick={() => toggle(!open)} ref={forwardedRef} ref={svgRef}>
                    <animated.svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 6749.87 6409.04" style={{ transform, opacity }} >
                         <defs>
                              <filter id="water">
                                   <AnimFeTurbulence type="fractalNoise" baseFrequency={freq} numOctaves="1.5" result="TURB" seed="8" />
                                   <AnimFeDisplacementMap xChannelSelector="R" yChannelSelector="G" in="SourceGraphic" in2="TURB" result="DISP" scale={scale} />
                              </filter>
                         </defs>

                         <g id="Layer_2" data-name="Layer 2" filter="url(#water)">
                              <animated.path className="cls-1" d="M3688.22,17.32C1832.47-165.09,187.82,1114,14.8,2874.24S1205.89,6209.31,3061.65,6391.72,6562,5295.05,6735.07,3534.8,5544,199.73,3688.22,17.32ZM6673.7,3528.77c-169.86,1728.1-1784.47,2983.82-3606.33,2804.75S-93.7,4608.36,76.16,2880.27,1860.64-103.56,3682.5,75.52,6843.56,1800.68,6673.7,3528.77Z" />
                         </g>

                    </animated.svg>
               </div>
          </div>
     )
}
