import React, { Component, useRef, useEffect, useState } from 'react';
import Slider from 'react-slick'
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import SamplePrevArrow from './SampleArrow/SamplePrevArrow'
import SampleNextArrow from './SampleArrow/SampleNextArrow'


const Product = React.forwardRef((props, ref) => {
     // const { appRef } = props;
     
     function importAll(r) {
          let images = {};
          r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
          return images;
     }
     const images = importAll(require.context('../images/product', false, /\.(png|jpe?g|svg)$/));
    
     const SampleBeforeChange = (oldIndex, newIndex) =>{
          let activeElem = document.querySelectorAll('.product .slick-slide')[newIndex].querySelector('article').getAttribute("product");
          
          ref.current.removeAttribute('id');
          ref.current.id = activeElem;
          
          document.querySelector('.body--back .svg--circular').click()


        
     }
     

     // next and prevArrow

     const sliderRef = useRef(null)

     const keyPressSlick = (event) =>{
          event.preventDefault();
          event.stopPropagation();
          if (event.keyCode === 39 || event.key === 'd'){
               console.log('next')
               sliderRef.current.slickNext()

          }
          else if (event.keyCode === 37 || event.key === 'a') {
               console.log('prev')
               sliderRef.current.slickPrev()

          }

     }


     // document.addEventListener("keydown", keyPressSlick, false);


     const settings = {
          className: "center",
          centerMode: true,
          infinite: false,
          centerPadding: "25%",
          slidesToShow: 1,
          speed: 500,
          dots: true,
          arrow:true,
          draggable:false,
          accessibility: false,
          pauseOnDotsHover:true,
          pauseOnFocus: true,
          nextArrow: <SampleNextArrow />,
          prevArrow: <SamplePrevArrow />,
          beforeChange: SampleBeforeChange,
     };
     const play = () =>{
          this.slider.slickPlay();
     }
     const pause=() => {
          this.slider.slickPause();
     }

     
     
     
     
     return (
          <div className="product" onKeyDown={keyPressSlick}>
               <Slider {...settings} ref={sliderRef}>
          {Object.keys(images).map(function (key, index) {
               return (
                    <article className='item' key={index} product={'product' + index}>
                    <div className="card--img">
                    <img src={images[key].default} />
                    </div>
                    </article>
                    )
               })}
          </Slider>
               </div>
               );
          }
)
          
          export default Product;
          